import { DeleteOutlined, ShopOutlined, TeamOutlined } from '@ant-design/icons'
import { Button, Card, Col, DatePicker, Form, Input, Modal, Popconfirm, Row, Select, Space, Table, Tooltip } from 'antd'
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectProduct } from '../../select/SelectProduct'
import GetColumnSearchProps from '../../utils/getColumnSearchProps';

const { Option } = Select;

export default function OrderStatusAPIPReport(props) {

    const productOrderList = useStoreState(state => state.order.productOrderList);

    const fetchproductOrderList = useStoreActions(state => state.order.fetchproductOrderList);


    const [form] = Form.useForm();

    const onFinish = (values: any) => {
        fetchproductOrderList(values?.orderStatus);
    };


    useEffect(() => {
        setPageTitle();
    }, [])

    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'Report'
        return pageTitleEle ? pageTitleEle.innerHTML = pageTitle : ''
    }

    const columns = [
        {
            title: 'Products Name',
            dataIndex: 'productName',
            key: 'productName',
            ...GetColumnSearchProps('productName', 'Product Name'),
        },
        {
            title: 'Package Name',
            dataIndex: 'packageName',
            key: 'packageName',
            ...GetColumnSearchProps('packageName', 'Package Name'),
        },        
        {
            title: 'Partner Name',
            dataIndex: 'partnerName',
            key: 'partnerName',
            ...GetColumnSearchProps('partnerName', 'Partner Name'),
        },       
        {
            title: 'Partner Area',
            dataIndex: 'area',
            key: 'area',
            ...GetColumnSearchProps('area', 'Partner Area'),
        },
        {
            title: 'Client Name',
            dataIndex: 'clientName',
            key: 'clientName',
            ...GetColumnSearchProps('clientName', 'Client Name'),
        },
        {
            title: 'Contact No',
            dataIndex: 'contactNo',
            key: 'contactNo',
            ...GetColumnSearchProps('contactNo', 'Contact No'),
        },
        {
            title: 'Order Date',
            dataIndex: 'orderDate',
            key: 'orderDate',
            ...GetColumnSearchProps('orderDate', 'Order Date'),
        },
        {
            title: 'Status',
            dataIndex: 'approveStatus',
            key: 'approveStatus',
            ...GetColumnSearchProps('approveStatus', 'Approve Status'),
        }
    ];



    return (
        <>
            <Form layout="vertical" onFinish={onFinish} id='create-class' form={form} >
                <Row gutter={15} className="box-shadow p-15">
                    <Col xs={24} sm={24} md={24} lg={8} xl={8}></Col>

                    <Col xs={24} sm={24} md={24} lg={5} xl={5}>

                        <Form.Item
                            name="orderStatus"
                            label="Order Status:"
                            className="title-Text"
                            rules={[
                                { required: true, message: "Please select your status" },
                            ]}
                        >
                            <Select placeholder="Order Status" style={{ width: "100" }} >
                                <Option value="0">Pending</Option>
                                <Option value="1">Approved</Option>
                                <Option value="2">Pre-approved</Option>

                            </Select>

                        </Form.Item>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={4} xl={4}>

                        <Space size="small" style={{ marginTop: 30 }} >
                            <Button type="primary" htmlType="submit" >
                                Search
                                    </Button>
                        </Space>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={5} xl={5}></Col>
                </Row>
            </Form>
            {productOrderList.length > 0 &&
                <Card title="Order Status" style={{overflow:"auto"}} >

                    <Table bordered={true} dataSource={productOrderList} columns={columns} />

                </Card>

            }
        </>
    )
}
