import { Button, Card, Col, DatePicker, Form, Input, InputNumber, Row, Select, Space } from 'antd'
import React, { useEffect, useState } from 'react'
import { SelectProduct } from '../../select/SelectProduct'
import moment from "moment";
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectClient } from '../../select/SelectClient';

const { Option } = Select;
const { RangePicker } = DatePicker;
const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};
export default function BillGenerateSuperAdmin(props) {

    const saveProductBillGenerateYearly = useStoreActions(state => state.bill.saveProductBillGenerateYearly)
    const fetchclientUnit = useStoreActions(state => state.bill.fetchclientUnit)
    const clientUnit = useStoreState(state => state.bill.clientUnit);
    const [billgenerateForm] = Form.useForm();

    const orderFormSubmit = (value) => {
        //console.log(value)
        let postYearly = {
            clientUnit: clientUnit,
            clientId: value?.client?.clientId,
            months: value?.month,
            year: value?.year,
            productRate: num,
        }
        console.log(postYearly);

        saveProductBillGenerateYearly(postYearly);


    };

    useEffect(() => {
        setPageTitle()
    }, []);

    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'Bill Generate Yearly'
        return pageTitleEle ?  pageTitleEle.innerHTML = pageTitle : ''      
    }

    const [productId, setProductId] = useState<any>(null);
    const [clientVal, setClientVal] = useState<any>(null);

    const [monthLength, setmonthLength] = useState<any>(0)
    const [num, setNum] = useState<any>(0)

    const onchangesetNum = (val) => {
        setNum(val)
    }

    const onchangemonthLength = (val) => {
        setmonthLength(val.length)
    }

    const onchangeproductId = (val) => {
        setProductId(val);
        setClientVal(null)
    }
    const onchangeClient = (val) => {
        setClientVal(val)
        fetchclientUnit(val?.clientId);
    }

    let year = new Date().getFullYear();

    const [total, settotal] = useState<any>(0);

    useEffect(() => {
        settotal(parseFloat(clientUnit) * monthLength * num)
    }, [clientUnit, monthLength, num])
    return (
        <>
            <Card title="Yearly Bill Generate Form">
            <Form
                layout="horizontal"
                {...layout}
                onFinish={orderFormSubmit}
                id="basic-info"
                form={billgenerateForm}
            >   
                <div className="__orderForm p-0 custom-form">
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="product_id"
                                label="Product:"
                                className="title-Text"
                                rules={[
                                    { required: true, message: "Please select your product" },
                                ]}
                            >
                                <SelectProduct onChange={(val) => onchangeproductId(val)} selected={productId} />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="client"
                                label="Client:"
                                className="title-Text"
                                rules={[
                                    { required: true, message: "Please select client" },
                                ]}
                            >
                                <SelectClient onChange={(val) => onchangeClient(val)} selected={clientVal} />
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="cunit"
                                label="Clinet Unit:"
                                className="title-Text custom-text-input"

                            >
                                <span>{clientUnit}</span>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row>
                    <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="price"
                                label="Product Price:"
                                className="title-Text"

                            >
                                <div className="price-wrapper"> <InputNumber min={0} placeholder="Insert price" onChange={(val) => onchangesetNum(val)} /><div className="price-title">TK</div></div>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="year"
                                label="Year:"
                                className="title-Text"
                                initialValue={year}
                                rules={[
                                    { required: true, message: "Please select year" },
                                ]}
                            >
                                <Select placeholder="Select Year" allowClear>
                                    <Option value={year - 1}>{year - 1}</Option>
                                    <Option value={year}>{year}</Option>
                                    <Option value={year + 1}>{year + 1}</Option>
                                </Select>

                            </Form.Item>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="month"
                                label="Month:"
                                className="title-Text"
                                rules={[
                                    { required: true, message: "Please select month" },
                                ]}
                            >
                                <Select placeholder="Select Month" mode="multiple" allowClear onChange={(val) => onchangemonthLength(val)}>
                                    <Option value="January">January</Option>
                                    <Option value="February">February</Option>
                                    <Option value="March">March</Option>
                                    <Option value="April">April</Option>
                                    <Option value="May">May</Option>
                                    <Option value="June">June</Option>
                                    <Option value="July">July</Option>
                                    <Option value="August">August</Option>
                                    <Option value="September">September</Option>
                                    <Option value="October">October</Option>
                                    <Option value="November">November</Option>
                                    <Option value="December">December</Option>
                                </Select>

                            </Form.Item>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Form.Item
                                name="total"
                                label="Total Value:"
                                className="title-Text custom-text-input"

                            >
                                <span>{total} Taka</span>
                            </Form.Item>
                        </Col> 
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                            <Space size="small" style={{ float: "right"}}>
                                <Button type="primary" htmlType="submit" >
                                    Generate
                                </Button>
                            </Space>
                        </Col>
                    </Row>
                </div>   
            </Form>
            </Card>
        </>
    )
}
