import { DeleteOutlined, EyeOutlined, TeamOutlined, UserOutlined } from '@ant-design/icons'
import { Button, Card, Col, Form, Modal, Popconfirm, Row, Select, Space, Table, Tooltip } from 'antd'
import Avatar from 'antd/lib/avatar/avatar'
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectDistrict } from '../../select/SelectDistrict'
import { SelectThana } from '../../select/SelectThana'
const { Option } = Select;

export default function PendingListSuperAdmin(props) {

    const pendingList = useStoreState(state => state.partner.pendingList);
    const underPartnerList = useStoreState(state => state.partner.underPartnerList);
    const fetchPendingList = useStoreActions(state => state.partner.fetchPendingList);
    const fetchpartnerListByLevel = useStoreActions(state => state.partner.fetchpartnerListByLevel);
    const partnerAssign = useStoreActions(state => state.partner.partnerAssign);
    const applicantPartnerDelete = useStoreActions(state => state.partner.applicantPartnerDelete);
    const [assignForm] = Form.useForm();

    useEffect(() => {
        fetchPendingList();
        setPageTitle();
    }, [])

    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'Pending Applied'
        return pageTitleEle ? pageTitleEle.innerHTML = pageTitle : ''
    }

    const onchangePartnerLevel = (val) => {
        // console.log(val)
        fetchpartnerListByLevel(val)
        assignForm.setFieldsValue({
            under_partner: null,
        });
    };
    const [assignModal, setassignModal] = useState<boolean>(false)

    const onchangeDistrict = (val) => {
        //   console.log(val)
        setThanaId(null);
        setdisId(val)
    };
    const [disId, setdisId] = useState<any>(null);

    const onchangeThana = (val) => {
        //   console.log(val)
        setThanaId(val)
    };
    const [thanaId, setThanaId] = useState<any>(null);
    const [rowId, setRowID] = useState<any>(null);


    const [underPartnerid, setunderPartnerid] = useState<any>(null);

    const onchangeUnderPartnerLevel = (val) => {
        setunderPartnerid(val)
    };


    const assignFormSubmit = (value) => {
        let postsubmit: any = {};
        postsubmit.areaId = value?.thana_id?.thanaId;
        postsubmit.parentId = value?.under_partner;
        postsubmit.partnerId = rowId;
        postsubmit.partnerLevel = parseInt(value?.partner_level);
        // console.log(postsubmit);
        partnerAssign(postsubmit);
        setassignModal(false);
    };

    const deletePartner = value => {
        applicantPartnerDelete(value)
    }
    const partnerProfile = useStoreState(state => state.partner.partnerProfile);
    const fetechpartnerProfile = useStoreActions(state => state.partner.fetchpartnerProfile);

    const [isModalVisible, setIsModalVisible] = useState(false);
    const [loading, setLoading] = useState(false);

    const showModal = (value) => {
        setLoading(true);
        fetechpartnerProfile(value);
        setIsModalVisible(true);
        setTimeout(() => {
            setLoading(false);
        }, 1000);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };
    const columns = [
        {
            title: 'Applicant Name',
            dataIndex: 'applicantName',
            key: 'applicantName',
        },
        {
            title: 'Mobile No.',
            dataIndex: 'mobileNo',
            key: 'mobileNo',
        },
        {
            title: 'Designation',
            dataIndex: 'designation',
            key: 'designation',
        },
        {
            title: 'Company Name',
            dataIndex: 'companyName',
            key: 'companyName',
        },
        {
            title: 'Company Address',
            dataIndex: 'companyAddress',
            key: 'companyAddress',
        },
        {
            title: 'District',
            dataIndex: 'zilla',
            key: 'zilla',
        },
        {
            title: 'Thana',
            dataIndex: 'thana',
            key: 'thana',
        },
        {
            title: 'View',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    <Tooltip title="Pre-Approve Order">
                        <Button type="primary" icon={<EyeOutlined />} onClick={() => { showModal(record.partnerId) }}>View</Button>
                    </Tooltip>
                </Space>
            ),
        },
        {
            title: 'Assign',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    <Tooltip title="Assign Partner">
                        <Button type="primary" icon={<TeamOutlined />} onClick={() => { (setassignModal(true)); setRowID(record?.partnerId) }}>Assign</Button>
                    </Tooltip>
                </Space>
            ),
        },
        {
            title: 'Action',
            render: (text: any, record: any, index) => (
                <Space size="middle">

                    <Popconfirm
                        title="Are you sure to delete this?"
                        okText="Yes"
                        cancelText="No"
                        onConfirm={() => deletePartner(record?.partnerId)}
                    >
                        <Tooltip title="Delete">
                            <Button danger icon={<DeleteOutlined />} />
                        </Tooltip>
                    </Popconfirm>

                </Space>
            ),
        },
    ]
    const columns2 = [
        {
            title: 'Exam Title',
            dataIndex: 'examTitle',

        },
        {
            title: 'Major/Group',
            dataIndex: 'major',
        },
        {
            title: 'Passing Year',
            dataIndex: 'passingYear',
        },
        {
            title: 'Duration',
            dataIndex: 'duration',
        },
        {
            title: 'Institute/University',
            dataIndex: 'instituteName',
        },
        {
            title: 'Result',
            dataIndex: 'result',
        },
    ]

    return (
        <>
            <Card title="Applied Pending List" >
                <Table bordered={true} dataSource={pendingList} columns={columns} />
                <Modal
                    title="Assign Partner"
                    centered
                    visible={assignModal}
                    onOk={() => {setassignModal(false); assignForm.resetFields(); setdisId(null); setThanaId(null)}}
                    onCancel={() => {setassignModal(false); assignForm.resetFields();setdisId(null); setThanaId(null)}}
                    cancelButtonProps={{ style: { display: 'none' } }}
                    okButtonProps={{ style: { display: 'none' } }}
                >
                    <Form
                        layout="vertical"
                        onFinish={assignFormSubmit}
                        id="basic-info"
                        form={assignForm}
                    >
                        <Row gutter={8}>
                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="partner_level"
                                    label="Partner Level:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select level" },
                                    ]}
                                >
                                    <Select placeholder="Select Under Partner" allowClear onChange={onchangePartnerLevel}>
                                        <Option value="2">PIP</Option>
                                        <Option value="3">APIP</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={1} xl={1}></Col>

                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="under_partner"
                                    label="Under Partner:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select under partner" },
                                    ]}
                                >
                                    <Select placeholder="Select Under Partner" allowClear options={underPartnerList} value={underPartnerid} onChange={onchangeUnderPartnerLevel}>

                                    </Select>
                                </Form.Item>
                            </Col>

                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="district_id"
                                    label="District:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select your district" },
                                    ]}
                                >
                                    <SelectDistrict onChange={(val) => onchangeDistrict(val)} selected={disId}

                                    />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={1} xl={1}></Col>

                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="thana_id"
                                    label="Area:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select your area" },
                                    ]}
                                >
                                    <SelectThana onChange={(val) => onchangeThana(val)} selected={thanaId}

                                    />
                                </Form.Item>

                            </Col>
                        </Row>
                        <Space size="small" style={{ float: "right" }}>
                            <Button type="primary" htmlType="submit" >
                                Assign
                            </Button>
                        </Space>
                        <br />
                    </Form>

                </Modal>
                <Modal
                    title="" visible={isModalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    cancelButtonProps={{ style: { display: 'none' } }}
                    okButtonProps={{ style: { display: 'none' } }}
                    centered
                    width={"80%"}
                    style={{ width: "80%", overflowY: 'scroll' }}
                >
                    <br />
                    <Card title="Basic Information" loading={loading} headStyle={{ background: "#FAFAFA" }} >
                        <Row gutter={8}>

                            <Col xs={24} sm={24} md={24} lg={9} xl={9} >
                                <div className="card-block">
                                    <p className="f-w-601">Name: {partnerProfile?.partnerName}</p>
                                    <p className="f-w-601">Father's Name: {partnerProfile?.fatherName}</p>
                                    <p className="f-w-601">Personal Number: {partnerProfile?.personalMobileNo} </p>
                                    <p className="f-w-601">Corporate Number: {partnerProfile?.corporateMobileNo}</p>
                                    <p className="f-w-601">Email: {partnerProfile?.email}</p>
                                </div>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={9} xl={9} >
                                <div className="card-block">
                                    <p style={{ color: "white" }}>val </p>
                                    <p className="f-w-601">Mother's Name: {partnerProfile?.motherName}</p>
                                    <p className="f-w-601">TIN No: {partnerProfile?.tinNo}</p>
                                    <p className="f-w-601">NID No: {partnerProfile?.nationalIdNo}</p>
                                    <p className="f-w-601">Date of Birth: {partnerProfile?.birthDate}</p>
                                </div>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={6} xl={6} >
                                <Avatar size={200} icon={<UserOutlined />} shape={"square"} />
                            </Col>
                        </Row>
                    </Card>
                    <br />
                    <Card title="Education Information" loading={loading} headStyle={{ background: "#FAFAFA" }} >
                        <Table bordered={true} dataSource={partnerProfile?.educationInfos} columns={columns2} pagination={false} />
                    </Card>
                    <br />
                    <Card title="Company Information" loading={loading} headStyle={{ background: "#FAFAFA" }}>
                        <Row gutter={8}>
                            <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                                <div className="card-block">
                                    <p className="f-w-601">Company Name: {partnerProfile?.companyName}</p>
                                    <p className="f-w-601">Trade License No: {partnerProfile?.tradeLicenceNo}</p>
                                </div>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                                <div className="card-block">
                                    <p className="f-w-601">Company Address: {partnerProfile?.companyAddress}</p>
                                    <p className="f-w-601">Business TIN No: {partnerProfile?.companyTinNo}</p>
                                </div>
                            </Col>
                        </Row>
                    </Card>
                    <br />
                    <Card title="Account Information" loading={loading} headStyle={{ background: "#FAFAFA" }}>
                        <Row gutter={8}>
                            <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                                <div className="card-block">
                                    <p className="f-w-601">Account Holder Name: {partnerProfile?.accountHolderName}</p>
                                    <p className="f-w-601">Bank Name: {partnerProfile?.bankName}</p>
                                    <p className="f-w-601">Branch Name: {partnerProfile?.branchName}</p>
                                    <p className="f-w-601">Account No: {partnerProfile?.accountNo}</p>
                                </div>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                                <div className="card-block">
                                    <p className="f-w-601">Swift Code: {partnerProfile?.swiftCode}</p>
                                    <p className="f-w-601">Routing No: {partnerProfile?.routingNo}</p>
                                    <p className="f-w-601">Account Type: {partnerProfile?.accountType}</p>
                                    <p className="f-w-601">Mobile No(Bank Account): {partnerProfile?.corporateMobileNo}</p>
                                </div>
                            </Col>
                        </Row>
                    </Card>
                </Modal>

            </Card>

        </>
    )
}
