import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy';
import { Steps, Form, Input, Row, Col, InputNumber, Button, DatePicker, Upload, message, Tooltip, Table, Select, Divider, Space, Popconfirm } from 'antd';
import { ArrowLeftOutlined, ArrowRightOutlined, DeleteOutlined, PlusOutlined, SendOutlined, UploadOutlined } from '@ant-design/icons';
import logo from "../../../../../src/assets/images/suite_logo.png";
import { SelectDistrict } from '../../select/SelectDistrict'
import { SelectThana } from '../../select/SelectThana'
import { v4 as uuidv4 } from "uuid";
import { Link } from "react-router-dom";
import moment from "moment";
import ImgCrop from 'antd-img-crop';
const { Step } = Steps;
const { Option } = Select;
let examTitleIndex = 0;
let examMajorIndex = 0;

export default function Signup(props) {

    const partnerSignup = useStoreActions(state => state.partner.partnerSignup);
    const signUpStatus = useStoreState(state => state.partner.signUpStatus);
    const [step, setStep] = useState<number>(1);
    const [signUpData, setSignUpData] = useState<any>()
    const [basicInfo] = Form.useForm();
    const [companyInfo] = Form.useForm();
    const [bankInfo] = Form.useForm();
    const [fileSave, setfileSave] = useState<boolean>(false);
    const [fileName, setfileName] = useState<any>("");
    const [fileContent, setfileContent] = useState<any>("");
    const [disBtn, setdisBtn] = useState<boolean>(false);

    const basicInfoForm = (value) => {
        value.fileSave = fileSave;
        value.businessIdentificationNo = "123654789";
        value.fileName = fileSave;
        value.fileName = fileName;
        value.designation = value?.designation;
        value.fileContent = fileContent;
        value.birthDate = moment(value?.birthDate).format("YYYY-MM-DD");
        value.thanaId = value.thanaId?.thanaId;
        value.corporateMobileNo = value.corporateMobileNo == undefined ? "" : value.corporateMobileNo;
        value.postOffice = value.postOffice == undefined ? "" : value.postOffice;
        value.village = value.village == undefined ? "" : value.village;
        delete value.user_image;
        delete value.district_id;
        setSignUpData(value);
        setStep(2);
    };
    const companyInfoForm = (value) => {
        //console.log(value)
        value.companyAddress = value.companyAddress == undefined ? "" : value.companyAddress;
        value.companyName = value.companyName == undefined ? "" : value.companyName;
        value.companyTinNo = value.companyTinNo == undefined ? "" : value.companyTinNo;
        value.tradeLicenceNo = value.tradeLicenceNo == undefined ? "" : value.tradeLicenceNo;
        let temp = Object.assign(signUpData, value)
        setSignUpData(temp);
        //console.log(signUpData)
        setStep(4);
    };

    const bankInfoForm = (value) => {
        value.swiftCode = value.swiftCode == undefined ? "" : value.swiftCode;
        let temp = Object.assign(signUpData, value)
        setSignUpData(temp);
        //console.log(temp);
        partnerSignup(temp);
        // setStep(4)
    };

    useEffect(() => {
        if (signUpStatus) {
            window.location.href = '/success'
        }
    }, [signUpStatus])

    const onchangeDistrict = (val) => {
        //   console.log(val)
        setThanaId(null);
        setdisId(val);
    }
    const [disId, setdisId] = useState<any>(null);

    const onchangeThana = (val) => {
        //   console.log(val)
        setThanaId(val);
    }
    const [thanaId, setThanaId] = useState<any>(null);

    const [educationData, seteducationData] = useState<any>([]);

    const newObj = {
        key: uuidv4(),
        exam_title: '',
        major: '',
        institute: '',
        result: '',
        passing_year: '',
        duration: '',
    };

    useEffect(() => {
        seteducationData([newObj]);
    }, [])

    // console.log(educationData);

    const [examTitle, setExamTitle] = useState<any>(['SSC', 'HSC', 'BSC', 'BA', 'MSC', 'MA']);
    const [examName, setexamName] = useState<any>('');
    const [examMajor, setexamMajor] = useState<any>(['Science ', 'Arts', 'Commerce']);
    const [majorName, setmajorName] = useState<any>('');

    const onExamNameChange = event => {
        setexamName(event.target.value)
    };

    const addItemExamName = () => {
        setExamTitle([...examTitle, examName || `Other ${examTitleIndex++}`])
        setexamName('')
    };
    const onMajorNameChange = event => {
        setmajorName(event.target.value)
    };

    const addItemMajorName = () => {
        setexamMajor([...examMajor, majorName || `Other ${examMajorIndex++}`])
        setmajorName('')
    };

    const onValueChangeSelect: any = (key, index) => (e: any) => {
        const newData = [...educationData];
        newData[index][key] = e;
        seteducationData(newData);
    };

    const onValueChangeInput: any = (key, index) => (e: any) => {
        const newData = [...educationData];
        newData[index][key] = e.target.value;
        seteducationData(newData);
    };

    // console.log(educationData);

    const handleAddRow = (data) => {
        //  console.log(data)
        if (data.exam_title == '' || data.duration == '' || data.institute == '' || data.major == '' || data.passing_year == '' || data.result == '') {
            message.error('Please insert in all fields');
        } else {
            const newData = [...educationData, newObj];
            seteducationData(newData);
        }
    }
    const handleDeleteExam = (key) => {
        const temp = educationData.filter(item => item?.key != key)
        seteducationData(temp);
    }
    const [yearList, setYearList] = useState<any>([2010, 2011])

    function yearOption() {
        let yearArr: any = [];
        for (let k = 1975; k <= (new Date().getFullYear()); k++) {
            yearArr.push(k)
        }
        //  console.log(new Date().getFullYear()-1975);
        setYearList(yearArr)

    }
    useEffect(() => {
        yearOption();
    }, [])
    const educationCol = [
        {
            title: 'Exam Title',
            //  width: "20%",
            // dataIndex: 'exam_title',
            // key: 'exam_title',
            render: (text, record, index) => (
                <>
                    <Select
                        style={{ width: "100%" }}
                        placeholder="Select Exam"
                        showSearch
                        defaultValue={record?.exam_title == '' ? null : record?.exam_title}
                        onChange={onValueChangeSelect("exam_title", index)}
                        dropdownRender={menu => (
                            <div>
                                {menu}
                                <Divider style={{ margin: '4px 0' }} />
                                <div style={{ display: 'flex', flexWrap: 'nowrap', padding: 8 }}>
                                    <Input style={{ flex: 'auto' }} value={examName} onChange={onExamNameChange} />
                                    <a
                                        style={{ flex: 'none', padding: '8px', display: 'block', cursor: 'pointer' }}
                                        onClick={addItemExamName}
                                    >
                                        <PlusOutlined />
                                    </a>
                                </div>
                            </div>
                        )}
                    >
                        options=      {examTitle.map(item => (
                        <Option key={item} value={item}>{item}</Option>
                    ))}
                    </Select>


                </>
            ),
        },
        {
            title: 'Major/Group',
            //dataIndex: 'major',
            // key: 'major',
            render: (text, record, index) => (
                <>
                    <Select
                        style={{ width: "100%" }}
                        placeholder="Select Major"
                        showSearch
                        defaultValue={record?.major == '' ? null : record?.major}
                        onChange={onValueChangeSelect("major", index)}
                        dropdownRender={menu => (
                            <div>
                                {menu}
                                <Divider style={{ margin: '4px 0' }} />
                                <div style={{ display: 'flex', flexWrap: 'nowrap', padding: 8 }}>
                                    <Input style={{ flex: 'auto' }} value={majorName} onChange={onMajorNameChange} />
                                    <a
                                        style={{ flex: 'none', padding: '8px', display: 'block', cursor: 'pointer' }}
                                        onClick={addItemMajorName}
                                    >
                                        <PlusOutlined />
                                    </a>
                                </div>
                            </div>
                        )}
                    >
                        options=      {examMajor.map(item => (
                        <Option key={item} value={item}>{item}</Option>
                    ))}
                    </Select>


                </>
            ),
        },
        {
            title: 'Passing Year',
            // dataIndex: 'passing_year',
            //key: 'passing_year',
            render: (text, record, index) => (
                <>
                    <Select
                        style={{ width: "100%" }}
                        placeholder="Select Year"
                        showSearch
                        defaultValue={record?.passing_year == '' ? null : record?.passing_year}
                        onChange={onValueChangeSelect("passing_year", index)}

                    >
                        options=      {yearList.map(item => (
                        <Option key={item} value={item}>{item}</Option>
                    ))}
                    </Select>


                </>
            ),
        },
        {
            title: 'Duration (Year)',
            // dataIndex: 'duration',
            // key: 'duration',
            render: (text, record, index) => (
                <>
                    <Input
                        placeholder="Duration"
                        maxLength={2}
                        defaultValue={record?.duration == '' ? null : record?.duration}
                        onChange={onValueChangeInput("duration", index)}
                    />

                </>
            ),
        },
        {
            title: 'Institute Name',
            //dataIndex: 'institute',
            // key: 'institute',
            render: (text, record, index) => (
                <>
                    <Input
                        placeholder="Institute"
                        maxLength={100}
                        defaultValue={record?.institute == '' ? null : record?.institute}
                        onChange={onValueChangeInput("institute", index)}
                    />

                </>
            ),
        },
        {
            title: 'Result',
            //dataIndex: 'result',
            //key: 'result',
            render: (text, record, index) => (
                <>
                    <Input
                        placeholder="Result"
                        maxLength={10}
                        defaultValue={record?.result == '' ? null : record?.result}
                        onChange={onValueChangeInput("result", index)}
                    />

                </>
            ),
        },

        {
            title: 'Action',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    {educationData.length == index + 1 &&
                        <Tooltip title={"Add Record"}>
                            <Button type={"primary"} icon={<PlusOutlined />} onClick={() => handleAddRow(record)} />
                        </Tooltip>
                    }
                    {educationData.length > 1 && educationData.length == index + 1 &&
                        <Popconfirm
                            title="Are you sure to delete?"
                            okText="Yes"
                            cancelText="No"
                            onConfirm={() => handleDeleteExam(record?.key)}
                        >
                            <Tooltip title="Delete">
                                <Button danger icon={<DeleteOutlined />} />
                            </Tooltip>
                        </Popconfirm>
                    }
                </Space>
            ),
        },
    ];
    const prevEducation = () => {
        setStep(1)
    };
    const prevCompanyInfo = () => {
        setStep(2)
        // console.log(educationData)
        // seteducationData(educationData)
    };
    const prevBankInfo = () => {
        setStep(3)
    };
    const nextEducation = () => {
        if (educationData[0]?.exam_title == '' || educationData[0]?.duration == '' || educationData[0]?.institute == '' || educationData[0]?.major == '' || educationData[0]?.passing_year == '' || educationData[0]?.result == '') {
            message.error("Please insert minimum one educational background")
        } else {

            let filtereducationData = educationData?.map(function (item) {
                return {
                    "duration": item.duration,
                    "examTitle": item.exam_title,
                    "instituteName": item.institute,
                    "major": item.major,
                    "passingYear": item.passing_year,
                    "result": item.result,
                }
            })
            signUpData.educationInfos = filtereducationData;
            setStep(3)
        }
    };
    const [fileList, setFileList] = useState<any>([
    ]);

    const onChangeFile = ({ fileList: newFileList }) => {
        // console.log(newFileList);
        if (newFileList.length == 0) {
            setfileSave(false);
            setfileName("");
            setfileContent("");
        }
        if (newFileList.length == 1) {
            newFileList[0].status = "done"
            setfileSave(true);
            setfileName(newFileList[0]?.name);
            setdisBtn(true)
            setTimeout(() => {
                setfileContent(newFileList[0]?.thumbUrl.split(',')[1]);
                setdisBtn(false)
            }, 2000);

        }
        setFileList(newFileList);
    };

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    };

    const onPreview = async file => {
        let src = file.url;
        if (!src) {
            src = await new Promise(resolve => {
                const reader = new FileReader();
                reader.readAsDataURL(file.originFileObj);
                reader.onload = () => resolve(reader.result);
                //console.log(reader.result);
            });
        }
        const image = new Image();
        image.src = src;
        const imgWindow: any = window.open(src);
        imgWindow.document.write(image.outerHTML);
    };

    return (
        <div className="signupPage-wrapper-full">
            <div className="login-form-wrapper py-15">
                <div className="overlay-9 bg-white">
                    <div className="logo-wrapper">
                    <img src={logo} alt="login_logo" />
                </div>
                <div className="signupPage-wrapper">
                    <div className="signupPage">
                        <div className="loginBack text-center">
                            Already Registerd? <Link to="/login">Back to login</Link>
                        </div>
                        <Steps current={step - 1}>
                            <Step title="Basic Info" />
                            <Step title="Education Info" />
                            <Step title="Company Info" />
                            <Step title="Bank Info" />
                        </Steps>
                        {step === 1 &&
                            <Form
                                layout="horizontal"
                                {...layout}
                                onFinish={basicInfoForm}
                                id="basic-info"
                                form={basicInfo}
                                className="custom-form"
                                
                            >
                                <Row gutter={8}>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="partnerName"
                                            label="Name:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please write name" },
                                                {
                                                    max: 60,
                                                    message: "Name cannot be more than 60 characters.",
                                                },
                                            ]}
                                        >
                                            <Input type="text" placeholder="Name"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="fatherName"
                                            label="Father Name:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please write father name" },
                                                {
                                                    max: 60,
                                                    message: "Father name cannot be more than 60 characters.",
                                                },
                                            ]}
                                        >
                                            <Input type="text" placeholder="Father's Name"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="motherName"
                                            label="Mather Name:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please write mother name" },
                                                {
                                                    max: 60,
                                                    message: "Mother name cannot be more than 60 characters.",
                                                },
                                            ]}
                                        >
                                            <Input type="text" placeholder="Mother's Name"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="personalMobileNo"
                                            label="Personal Number:"
                                            className="title-Text"

                                            rules={[
                                                { required: true, message: "Please write your number", },
                                            ]}
                                        >
                                            <Input type="text" placeholder="01711....."></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="corporateMobileNo"
                                            label="Corporate Number:"
                                            className="title-Text"
                                            rules={[
                                                { required: false, message: "Please write your number", },

                                            ]}
                                        >
                                            <Input type="text" placeholder="01711....."></Input>

                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="email"
                                            label="Email:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please write your email address" },
                                                {
                                                    max: 60,
                                                    type: 'email',
                                                    message: "Pleaset Insert valid email.",
                                                },
                                            ]}
                                        >
                                            <Input type="text" placeholder="example@gmail.com"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="nationalIdNo"
                                            label="National Id:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please enter your national id" },
                                            ]}
                                        >
                                            <Input type="text" placeholder="NID"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="birthDate"
                                            label="Date of Birth:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please enter your date of birth" },
                                            ]}
                                        >
                                            <DatePicker placeholder="Date of birth" style={{ width: "100%" }} allowClear format={'YYYY-MM-DD'} />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="tinNo"
                                            label="TIN No:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please enter your TIN no." },
                                            ]}
                                        >
                                            <Input type="text" placeholder="TIN No"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="district_id"
                                            label="District:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please select your district", },
                                            ]}
                                        >
                                            <SelectDistrict onChange={(val) => onchangeDistrict(val)} selected={disId}

                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="thanaId"
                                            label="Thana:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please select your thana", },
                                            ]}
                                        >
                                            <SelectThana onChange={(val) => onchangeThana(val)} selected={thanaId}

                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="postOffice"
                                            label="Post Office:"
                                            className="title-Text"
                                        >
                                            <Input type="text" placeholder="Enter post office"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="postalCode"
                                            label="Postal Code:"
                                            className="title-Text"
                                        >
                                            <Input type="text" placeholder="Enter postaL code"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="village"
                                            label="Village:"
                                            className="title-Text"
                                        >
                                            <Input type="text" placeholder="Enter village"></Input>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="designation"
                                            label="Designation:"
                                            className="title-Text"
                                            rules={[
                                                { required: true, message: "Please select your designation", },
                                            ]}
                                        >
                                               <Select placeholder="Select Designation" style={{ width: "100%" }} allowClear>
      <Option value="PIP">PIP</Option>
      <Option value="APIP">APIP</Option>
    </Select>

                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={18} xl={18}>
                                        <Form.Item
                                            name="user_image"
                                            label="Photo:"
                                            className="title-Text"
                                        >
                                            <ImgCrop rotate>
                                                <Upload
                                                    //action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                                    listType="picture"
                                                    accept="image/png, image/jpeg"
                                                    fileList={fileList}
                                                    onChange={onChangeFile}
                                                    onPreview={onPreview}
                                                >
                                                    {fileList.length < 1 && <><Button icon={<UploadOutlined />}>Upload</Button></>}
                                                </Upload>
                                            </ImgCrop>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={18} lg={18}>
                                        <Space size="small" style={{ float: "right" }}>
                                            <Button type="primary" htmlType="submit" disabled={disBtn} icon={<ArrowRightOutlined />}>
                                                Next
                                            </Button>
                                        </Space>
                                    </Col>
                                </Row>
                            </Form>
                        }
                        {step === 2 &&
                            <>
                                <br />
                                <Table bordered={true} dataSource={educationData} columns={educationCol} />
                                <br />
                                <Space size="small" style={{ float: "right" }}>
                                    <Button type="primary" icon={<ArrowLeftOutlined />} onClick={() => prevEducation()}>
                                        Previous
                                    </Button>
                                    <Button type="primary" icon={<ArrowRightOutlined />} onClick={() => nextEducation()}>
                                        Next
                                    </Button>
                                </Space>

                            </>
                        }
                        {step === 3 &&
                            <>
                                <br />
                                <Form
                                    layout="vertical"
                                    onFinish={companyInfoForm}
                                    id="company-info"
                                    form={companyInfo}
                                >
                                    <Row gutter={8}>
                                        {/* //////////////// */}
                                        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                                            <Form.Item
                                                name="companyName"
                                                label="Company Name:"
                                                className="title-Text"
                                            >
                                                <Input type="text" placeholder="Company Name"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                                            <Form.Item
                                                name="companyAddress"
                                                label="Company Address:"
                                                className="title-Text"
                                            >
                                                <Input type="text" placeholder="Company Address"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                                            <Form.Item
                                                name="tradeLicenceNo"
                                                label="Trade License No:"
                                                className="title-Text"
                                            >
                                                <Input type="text" placeholder="Trade License No"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                                            <Form.Item
                                                name="companyTinNo"
                                                label="Business Tin No:"
                                                className="title-Text"
                                            >
                                                <Input type="text" placeholder="Tin No"></Input>
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <br />
                                    <Space size="small" style={{ float: "right" }}>
                                        <Button type="primary" icon={<ArrowLeftOutlined />} onClick={() => prevCompanyInfo()}>
                                            Previous
                                    </Button>
                                        <Button type="primary" icon={<ArrowRightOutlined />} htmlType="submit" >
                                            Next
                                    </Button>
                                    </Space>
                                </Form>
                            </>
                        }
                        {step === 4 &&
                            <>
                                <br />
                                <Form
                                    layout="vertical"
                                    onFinish={bankInfoForm}
                                    id="bank-info"
                                    form={bankInfo}
                                >
                                    <Row gutter={8}>
                                        {/* //////////////// */}
                                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                                            <Form.Item
                                                name="accountHolderName"
                                                label="Account Holder Name:"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter bank account name" },
                                                ]}
                                            >
                                                <Input type="text" placeholder="Account Holder Name"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                                            <Form.Item
                                                name="bankName"
                                                label="Bank Name:"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter bank name" },
                                                ]}
                                            >
                                                <Input type="text" placeholder="Bank Name"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                                            <Form.Item
                                                name="branchName"
                                                label="Branch Name:"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter bank's branch name" },
                                                ]}
                                            >
                                                <Input type="text" placeholder="Branch Name"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                                            <Form.Item
                                                name="accountNo"
                                                label="Account No:"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter account no" },
                                                ]}
                                            >
                                                <Input type="text" placeholder="Account No"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                                            <Form.Item
                                                name="swiftCode"
                                                label="Swift Code:"
                                                className="title-Text"

                                            >
                                                <Input type="text" placeholder="Swift Code"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={8} xl={8}>
                                            <Form.Item
                                                name="routingNo"
                                                label="Routing No:"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter routing no" },
                                                ]}
                                            >
                                                <Input type="text" placeholder="Routing No"></Input>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                                            <Form.Item
                                                name="accountType"
                                                label="Account Type:"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter your bank's registered mobile no" },
                                                ]}
                                            >
                                                <Select placeholder="Select Account Type" allowClear>
                                                    <Option value="savings">Savings</Option>
                                                    <Option value="current">Current</Option>
                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
                                            <Form.Item
                                                name="bankAccountMobileNo"
                                                label="Mobile No(Of Bank Account):"
                                                className="title-Text"
                                                rules={[
                                                    { required: true, message: "Please enter your bank's registered mobile no" },
                                                ]}
                                            >
                                                <Input type="text" placeholder="Mobile No"></Input>
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <br />
                                    <Space size="small" style={{ float: "right" }}>
                                        <Button type="primary" icon={<ArrowLeftOutlined />} onClick={() => prevBankInfo()}>
                                            Previous
                                    </Button>
                                        <Button type="primary" icon={<SendOutlined />} htmlType="submit" >
                                            Submit
                                    </Button>
                                    </Space>
                                </Form>
                            </>
                        }
                    </div>
                </div>
                </div>
            </div>
        </div>
    )
}
