import { DeleteOutlined, TeamOutlined } from '@ant-design/icons'
import { Button, Card, Col, Form, Modal, Popconfirm, Row, Select, Space, Table, Tooltip } from 'antd'
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'


export default function OrderListSuperAdmin(props) {

    const pendingProductOrderList = useStoreState(state => state.order.pendingProductOrderList);

    const fetchpendingProductOrderList = useStoreActions(state => state.order.fetchpendingProductOrderList);

    const deleteProductOrder = useStoreActions(state => state.order.deleteProductOrder);
    const preApproveProductOrderPost = useStoreActions(state => state.order.preApproveProductOrderPost);


    useEffect(() => {
        fetchpendingProductOrderList();
        setPageTitle();
    }, [])

    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'Pending Order'
        return pageTitleEle ? pageTitleEle.innerHTML = pageTitle : ''
    }

    const deletePartner = value => {
        deleteProductOrder(value)
    }
    const columns = [
        {
            title: 'Product Name',
            dataIndex: 'productName',
            key: 'productName',
        },
        {
            title: 'Package Name',
            dataIndex: 'packageName',
            key: 'packageName',
        },
        {
            title: 'Partner Name',
            dataIndex: 'partnerName',
            key: 'partnerName',
        },
        {
            title: 'Partner Area',
            dataIndex: 'area',
            key: 'area',
        },
        {
            title: 'Client Name',
            dataIndex: 'clientName',
            key: 'clientName',
        },
        {
            title: 'Category',
            dataIndex: 'category',
            key: 'category',
        },
        {
            title: 'Address',
            dataIndex: 'address',
            key: 'address',
        },
        {
            title: 'Admin Name',
            dataIndex: 'adminName',
            key: 'adminName',
            
        },
        {
            title: 'Contact No',
            dataIndex: 'contactNo',
            key: 'contactNo',
        },
        {
            title: 'Order Date',
            dataIndex: 'orderDate',
            key: 'orderDate',
            
        },
        {
            title: 'Approve',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    <Popconfirm
                        title="Are you sure to order this?"
                        okText="Yes"
                        cancelText="No"
                        onConfirm={() => preApproveProductOrderPost(record?.orderId)}
                    >
                        <Tooltip title="Pre-Approve Order">
                            <Button type="primary" icon={<TeamOutlined />} >Pre-Approve</Button>
                        </Tooltip>
                    </Popconfirm>
                </Space>
            ),
        },
        {
            title: 'Action',
            render: (text: any, record: any, index) => (
                <Space size="middle">

                    <Popconfirm
                        title="Are you sure to delete this?"
                        okText="Yes"
                        cancelText="No"
                        onConfirm={() => deletePartner(record?.orderId)}
                    >
                        <Tooltip title="Delete">
                            <Button danger icon={<DeleteOutlined />} />
                        </Tooltip>
                    </Popconfirm>

                </Space>
            ),
        },
    ]

    return (
        <>
            <Card title="Pending Order List" >
                <Table bordered={true} dataSource={pendingProductOrderList} columns={columns} />
            </Card>
        </>
    )
}
