import { Select } from "antd";
import * as React from "react";
import { useStoreActions, useStoreState } from "../../store/hooks/easyPeasy";
const { Option } = Select;

export interface SelectClient {
  onChange?: any;
  selected?: any;
  defaultSelected?: any;
  style?: any
}

export const SelectClient = ({
  onChange,
  selected,
  defaultSelected,
  style
}: SelectClient) => {

  const clientList = useStoreState((state) => state.order.clientList);
  const onSelect = (id) => {
    if (clientList) {
      const val = clientList.find((item) => item.clientId === id);
      onChange(val);
    }
  };

  return (
    <Select
      onChange={onSelect}
      // loading={loading}
      showSearch
      // allowClear
      defaultValue={defaultSelected}
      value={selected?.clientId}
      style={style}
      placeholder="Select Client"
      filterOption={(input, option) =>
        option !== undefined &&
        option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
      }
    >
      {clientList ? (
        clientList.map((type, idx) => (
          <Option key={type.clientId} value={type.clientId}>
            {type.clientName}
          </Option>
        ))
      ) : (
        <Option value="fetching">Fetching Clients</Option>
      )}
    </Select>
  );
};
