import { DeleteOutlined, EyeOutlined, InfoCircleOutlined, ShopOutlined, TeamOutlined, UserOutlined } from '@ant-design/icons'
import { Button, Card, Col, DatePicker, Divider, Form, Input, Modal, Popconfirm, Row, Select, Space, Table, Tooltip } from 'antd'
import Avatar from 'antd/lib/avatar/avatar'
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectProduct } from '../../select/SelectProduct'
import GetColumnSearchProps from '../../utils/getColumnSearchProps';

const { Option } = Select;

export default function ApprovedPartnerSDL(props) {

    const partnerList = useStoreState(state => state.partner.partnerList);

    const fetchpartnerList = useStoreActions(state => state.partner.fetchpartnerList);

    const partnerProfile = useStoreState(state => state.partner.partnerProfile);

    const fetechpartnerProfile = useStoreActions(state => state.partner.fetchpartnerProfile);


    const [form] = Form.useForm();

    const onFinish = (values: any) => {
        fetchpartnerList(values?.designationPartner);
    };


    useEffect(() => {
        setPageTitle();
    }, [])

    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'Approved Partner'
        return pageTitleEle ? pageTitleEle.innerHTML = pageTitle : ''
    }
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [loading, setLoading] = useState(false);

    const showModal = (value) => {
        setLoading(true);
        fetechpartnerProfile(value);
        setIsModalVisible(true);
        setTimeout(() => {
            setLoading(false);
        }, 800);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };


    const columns = [
        {
            title: 'Id',
            dataIndex: 'partnerCustomId',
            key: 'partnerCustomId',

        },
        {
            title: 'Partner Name',
            dataIndex: 'partnerName',
            key: 'partnerName',

        },
        {
            title: 'Mobile No',
            dataIndex: 'mobileNo',
            key: 'mobileNo',

        },
        {
            title: 'Designation',
            dataIndex: 'designation',
            key: 'designation',
        },
        {
            title: 'View',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    <Tooltip title="Pre-Approve Order">
                        <Button type="primary" icon={<EyeOutlined  />} onClick={() => { showModal(record.partnerId) }}>View</Button>
                    </Tooltip>
                </Space>
            ),
        },
    ];

    const columns2 = [
        {
            title: 'Exam Title',
            dataIndex: 'examTitle',

        },
        {
            title: 'Major/Group',
            dataIndex: 'major',
        },
        {
            title: 'Passing Year',
            dataIndex: 'passingYear',
        },
        {
            title: 'Duration',
            dataIndex: 'duration',
        },
        {
            title: 'Institute/University',
            dataIndex: 'instituteName',
        },
        {
            title: 'Result',
            dataIndex: 'result',
        },
    ]


    return (
        <>
            <Form layout="vertical" onFinish={onFinish} id='create-class' form={form} >
                <Row gutter={15} className="box-shadow p-15">
                    <Col xs={24} sm={24} md={24} lg={8} xl={8}></Col>

                    <Col xs={24} sm={24} md={24} lg={5} xl={5}>

                        <Form.Item
                            name="designationPartner"
                            label="Designation:"
                            className="title-Text"
                            rules={[
                                { required: true, message: "Please select your status" },
                            ]}
                        >
                            <Select placeholder="Select Designation" style={{ width: "100" }} >
                                <Option value="APIP">APIP</Option>
                                <Option value="PIP">PIP</Option>
                            </Select>

                        </Form.Item>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={4} xl={4}>

                        <Space size="small" style={{ marginTop: 30 }} >
                            <Button type="primary" htmlType="submit" >
                                Search
                            </Button>
                        </Space>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={5} xl={5}></Col>
                </Row>
            </Form>
            {partnerList.length > 0 &&
                <Card title="Partner List" >

                    <Table bordered={true} dataSource={partnerList} columns={columns} />

                </Card>

            }
            <Modal
                title="" visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
                cancelButtonProps={{ style: { display: 'none' } }}
                okButtonProps={{ style: { display: 'none' } }}
                centered
                width={"80%"}
                style={{ width: "80%", overflowY: 'scroll' }}
            >
                <br />
                <Card title="Basic Information" loading={loading} headStyle={{background:"#FAFAFA"}} >
                    <Row gutter={8}>

                        <Col xs={24} sm={24} md={24} lg={9} xl={9} >
                            <div className="card-block">
                                <p className="f-w-601">Name: {partnerProfile?.partnerName}</p>
                                <p className="f-w-601">Father's Name: {partnerProfile?.fatherName}</p>
                                <p className="f-w-601">Personal Number: {partnerProfile?.personalMobileNo} </p>
                                <p className="f-w-601">Corporate Number: {partnerProfile?.corporateMobileNo}</p>
                                <p className="f-w-601">Email: {partnerProfile?.email}</p>
                            </div>
                        </Col>
                        <Col xs={24} sm={24} md={24} lg={9} xl={9} >
                            <div className="card-block">
                                <p style={{ color: "white" }}>val </p>
                                <p className="f-w-601">Mother's Name: {partnerProfile?.motherName}</p>
                                <p className="f-w-601">TIN No: {partnerProfile?.tinNo}</p>
                                <p className="f-w-601">NID No: {partnerProfile?.nationalIdNo}</p>
                                <p className="f-w-601">Date of Birth: {partnerProfile?.birthDate}</p>
                            </div>
                        </Col>
                        <Col xs={24} sm={24} md={24} lg={6} xl={6} >
                            <Avatar size={200} icon={<UserOutlined />} shape={"square"} />
                        </Col>
                    </Row>
                </Card>
                <br />
                <Card title="Education Information" loading={loading} headStyle={{background:"#FAFAFA"}} >
                    <Table bordered={true} dataSource={partnerProfile?.educationInfos} columns={columns2} pagination={false} />
                </Card>
                <br />
                <Card title="Company Information" loading={loading} headStyle={{background:"#FAFAFA"}}>
                    <Row gutter={8}>
                        <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                            <div className="card-block">
                                <p className="f-w-601">Company Name: {partnerProfile?.companyName}</p>
                                <p className="f-w-601">Trade License No: {partnerProfile?.tradeLicenceNo}</p>
                            </div>
                        </Col>
                        <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                            <div className="card-block">
                                <p className="f-w-601">Company Address: {partnerProfile?.companyAddress}</p>
                                <p className="f-w-601">Business TIN No: {partnerProfile?.companyTinNo}</p>
                            </div>
                        </Col>
                    </Row>
                </Card>
                <br />
                <Card title="Account Information" loading={loading} headStyle={{background:"#FAFAFA"}}>
                    <Row gutter={8}>
                        <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                            <div className="card-block">
                                <p className="f-w-601">Account Holder Name: {partnerProfile?.accountHolderName}</p>
                                <p className="f-w-601">Bank Name: {partnerProfile?.bankName}</p>
                                <p className="f-w-601">Branch Name: {partnerProfile?.branchName}</p>
                                <p className="f-w-601">Account No: {partnerProfile?.accountNo}</p>
                            </div>
                        </Col>
                        <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                            <div className="card-block">
                                <p className="f-w-601">Swift Code: {partnerProfile?.swiftCode}</p>
                                <p className="f-w-601">Routing No: {partnerProfile?.routingNo}</p>
                                <p className="f-w-601">Account Type: {partnerProfile?.accountType}</p>
                                <p className="f-w-601">Mobile No(Bank Account): {partnerProfile?.corporateMobileNo}</p>
                            </div>
                        </Col>
                    </Row>
                </Card>
            </Modal>
        </>
    )
}
