import { CloudDownloadOutlined, DeleteOutlined, EditOutlined, SaveOutlined, ShopOutlined, TeamOutlined } from '@ant-design/icons'
import { Button, Card, Checkbox, Col, DatePicker, Form, Input, InputNumber, Modal, Popconfirm, Row, Select, Space, Table, Tag, Tooltip } from 'antd'
import TextArea from 'antd/lib/input/TextArea';
import Text from 'antd/lib/typography/Text';
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectPartner } from '../../select/SelectIPartner'
import { SelectMonth } from '../../select/SelectMonth';
import { SelectYear } from '../../select/SelectYear';

const { Option } = Select;


export default function IspBillListSDL(props) {


    const ispClientBillInfoForSdlList = useStoreState(state => state.isp.ispClientBillInfoForSdlList);
    const fetchispClientBillInfoForSdlList = useStoreActions(state => state.isp.fetchispClientBillInfoForSdlList);
    const downloadIspBillDisbursement = useStoreActions(state => state.isp.downloadIspBillDisbursement);

    const [payload, setpayload] = useState<any>({
        billingMonth: '',
        billingYear: '',
    })
    const [form] = Form.useForm();

    const onFinish = (values: any) => {
        let postData: any = {
            billingMonth: values.billingMonth,
            billingYear: values.billingYear,
        }
        setpayload(postData)
        fetchispClientBillInfoForSdlList(postData)
    };


    useEffect(() => {
        setPageTitle();
    }, []);


    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'ISP'
        return pageTitleEle ? pageTitleEle.innerHTML = pageTitle : ''
    }


    const columns1 = [

        {
            title: 'Partner Name',
            dataIndex: 'partnerName',
            key: 'partnerName',
        },
        {
            title: 'Area Name',
            dataIndex: 'areaName',
            key: 'areaName',
        },
        {
            title: 'Total Bill',
            dataIndex: 'pipWiseTotalBillAmount',
            key: 'pipWiseTotalBillAmount',
        },
    ]

    const subcolumns = [
        {
            title: 'Speed',
            dataIndex: 'packageName',
            key: 'packageName',
        },
        {
            title: 'Price',
            dataIndex: 'packagePrice',
            key: 'packagePrice',
        },
        {
            title: 'Client',
            dataIndex: 'totalClient',
            key: 'totalClient',
        },
        {
            title: 'Enabled Client',
            dataIndex: 'totalEnableClient',
            key: 'totalEnableClient',
        },
        {
            title: 'Disabled Client',
            dataIndex: 'totalDisableClient',
            key: 'totalDisableClient',
        },
        {
            title: 'Bill Amount',
            dataIndex: 'totalBillAmount',
            key: 'totalBillAmount',
        },
        {
            title: 'Disable Bill',
            dataIndex: 'totalDisableBill',
            key: 'totalDisableBill',
        },
        {
            title: 'Payable Bill',
            dataIndex: 'totalPayableBill',
            key: 'totalPayableBill',
        },
        {
            title: 'Paid Bill',
            dataIndex: 'totalPaidBill',
            key: 'totalPaidBill',
        },
        {
            title: 'Due Bill',
            dataIndex: 'totalDueBill',
            key: 'totalDueBill',
        },
        {
            title: 'SDL(60%)',
            dataIndex: 'sdlAmount',
            key: 'sdlAmount',
        },
        {
            title: 'PIP(40%)',
            dataIndex: 'pipAmount',
            key: 'pipAmount',
        },
        {
            title: 'PIP Paid Amount ',
            render: (value, row, index) => {
                const obj: any = {
                    children: 44,
                    props: {},
                };

                obj.props.rowSpan = 0;

                return obj;
            },
        },
        {
            title: 'PIP Due Amount ',
            render: (value, row, index) => {
                const obj: any = {
                    children: 44,
                    props: {},
                };

                obj.props.rowSpan = 0;

                return obj;
            },
        },



    ]



    return (
        <>
            <Form layout="vertical" onFinish={onFinish} id='create-class' form={form} >
                <Row gutter={15} className="box-shadow p-15">

                    <Col xs={24} sm={24} md={24} lg={5} xl={5}>



                    </Col>

                    <Col xs={24} sm={24} md={24} lg={5} xl={5}>

                        <Form.Item
                            name="billingMonth"
                            label="Month:"
                            className="title-Text"
                            rules={[
                                { required: true, message: "Please select month" },
                            ]}
                        >
                            <SelectMonth />


                        </Form.Item>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={5} xl={5}>

                        <Form.Item
                            name="billingYear"
                            label="Year:"
                            className="title-Text"
                            rules={[
                                { required: true, message: "Please select year" },
                            ]}
                        >
                            <SelectYear />


                        </Form.Item>

                    </Col>

                    <Col xs={24} sm={24} md={24} lg={4} xl={4}>

                        <Space size="small" style={{ marginTop: 30 }} >
                            <Button type="primary" htmlType="submit" >
                                Search
                            </Button>
                        </Space>

                    </Col>
                    <Col xs={24} sm={24} md={24} lg={5} xl={5}>


                    </Col>
                </Row>
            </Form>
            {ispClientBillInfoForSdlList?.length > 0 &&
                <Card title="Bill List" >


                    <Table
                        rowKey="partnerName"
                        bordered={true}
                        pagination={false}
                        dataSource={ispClientBillInfoForSdlList}
                        columns={columns1}
                        expandable={{
                            expandedRowRender: (record: any) => (
                                <div style={{ overflow: 'auto' }} >
                                    <Table
                                        rowKey="packageName"
                                        bordered={true}
                                        pagination={false}
                                        dataSource={record.packageWiseBillInfos}
                                        columns={subcolumns}
                                        style={{ overflow: 'auto' }}
                                        summary={pageData => {
                                            return (
                                                <>
                                                    <Table.Summary.Row>
                                                        <Table.Summary.Cell index={0}> <Text type="danger" strong>Total</Text></Table.Summary.Cell>
                                                        <Table.Summary.Cell index={1}>

                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={2}>
                                                            <Text type="danger" strong>{record.pipWiseTotalClient}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={3}>
                                                            <Text type="danger" strong>{record.pipWiseTotalEnableClient}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={4}>
                                                            <Text type="danger" strong>{record.pipWiseTotalDisableClient}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={5}>
                                                            <Text type="danger" strong>{record.pipWiseTotalBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={6}>
                                                            <Text type="danger" strong>{record.pipWiseTotalDisableBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={7}>
                                                            <Text type="danger" strong>{record.pipWiseTotalPayableBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={8}>
                                                            <Text type="danger" strong>{record.pipWiseTotalPaidBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={9}>
                                                            <Text type="danger" strong>{record.pipWiseTotalUnpaidBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={10}>
                                                            <Text type="danger" strong>{record.pipWiseTotalSdlBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={11}>
                                                            <Text type="danger" strong>{record.pipWiseTotalPipBillAmount}</Text>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={12}>
                                                            <div style={{ marginTop: "-50%" }}>
                                                                <Text type="danger" strong>{record.pipPaidAmount}</Text>
                                                            </div>
                                                        </Table.Summary.Cell>
                                                        <Table.Summary.Cell index={13}>
                                                            <div style={{ marginTop: "-50%" }}>
                                                                <Text type="danger" strong>{record.pipDueAmount}</Text>
                                                            </div>
                                                        </Table.Summary.Cell>
                                                    </Table.Summary.Row>

                                                </>
                                            );
                                        }}
                                    />
                                </div>
                            )

                        }}

                    />
                    <br />
                    <Button type="primary" icon={<SaveOutlined />} onClick={() => downloadIspBillDisbursement(payload)} style={{ float: 'right' }}> Download Report </Button>
                </Card>

            }

        </>
    )
}
